:root {
  --accent: #E4322B;
  --textColor: #4B4B4B;
  --bgColor: #F3F4F5;
  --padding: 14px;
  --margin: 24px;
  --maxWidth: 1458px;
}

@media screen and (min-width: 768px) {
  :root {
    --padding: 20px;
  }
}
@media screen and (min-width: 1024px) {
  :root {
    --padding: 36px;
    --margin: 40px;
  }
}
@media screen and (min-width: 1366px) {
  :root {
    --padding: 45px;
    --margin: 50px;
  }
}
@media screen and (min-width: 1600px) {
  :root {
    --margin: 80px;
  }
}
.svg-svg-tel, .svg-svg-prev, .svg-svg-next, .svg-svg-email, .svg-svg-city {
  background: url("../images/sprites/main.svg") no-repeat;
}

.svg-svg-city {
  background-position: 0 0;
  background-size: 261.5384615385% auto;
  width: 26px;
}

.svg-svg-city:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 100%;
}

.svg-svg-city-dims {
  width: 26px;
  height: 26px;
}

.svg-svg-email {
  background-position: 61.9047619048% 0;
  background-size: 261.5384615385% auto;
  width: 26px;
}

.svg-svg-email:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 100%;
}

.svg-svg-email-dims {
  width: 26px;
  height: 26px;
}

.svg-svg-next {
  background-position: 50% 72.2222222222%;
  background-size: 425% auto;
  width: 16px;
}

.svg-svg-next:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 100%;
}

.svg-svg-next-dims {
  width: 16px;
  height: 16px;
}

.svg-svg-prev {
  background-position: 100% 0;
  background-size: 425% auto;
  width: 16px;
}

.svg-svg-prev:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 100%;
}

.svg-svg-prev-dims {
  width: 16px;
  height: 16px;
}

.svg-svg-tel {
  background-position: 0 100%;
  background-size: 261.5384615385% auto;
  width: 26px;
}

.svg-svg-tel:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 100%;
}

.svg-svg-tel-dims {
  width: 26px;
  height: 26px;
}

.image-by-height {
  width: auto;
}

.image-by-height:before {
  content: none;
}

.image-by-height > svg {
  height: inherit;
}

.image-by-height > img {
  height: inherit !important;
  vertical-align: top !important;
  margin: 0 !important;
  padding: 0 !important;
  max-width: none !important;
  max-height: none !important;
  border: 0 !important;
  opacity: 0 !important;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.content-box-component {
  box-sizing: content-box;
}

body {
  display: flex;
  flex-direction: column;
  background: #FFF;
  font-family: "Inter", "Helvetica", Arial, sans-serif;
  width: 100%;
  min-width: 320px;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 300;
  font-size: 11px;
  line-height: 145%;
}
body.no-scroll {
  overflow: hidden;
}
@media screen and (min-width: 1024px) {
  body {
    font-size: 12px;
    line-height: 167%;
  }
}
@media screen and (min-width: 1366px) {
  body {
    font-size: 14px;
    line-height: 171%;
  }
}
@media screen and (min-width: 1600px) {
  body {
    font-size: 15px;
    line-height: 173%;
  }
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

picture {
  display: block;
}

.background-image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}
.background-image picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.background-image picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.main {
  flex-grow: 1;
}

.wrapper {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
}

.container {
  width: 100%;
  max-width: var(--maxWidth);
  padding: 0 var(--padding);
  margin: 0 auto;
}

.btn {
  width: 36px;
  min-width: 36px;
  height: 36px;
  padding: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
  background: var(--accent);
  overflow: hidden;
}
.btn svg {
  width: 26px;
  height: 26px;
}

.link {
  padding: 12px 20px;
  background: var(--accent);
  border-radius: 3px;
  font-weight: 600;
  font-size: 12px;
  line-height: 12px;
  text-align: center;
  color: #fff;
}

.header {
  background: #fff;
  position: relative;
  z-index: 100;
}
.header__lead {
  display: none;
}
.header__left {
  display: flex;
  align-items: center;
}
.header__right {
  width: calc(100% - 48px);
}
.header__burger {
  margin-right: 12px;
  flex-direction: column;
}
.header__burger span {
  width: 16px;
  height: 1px;
  margin-bottom: 5px;
  background: #fff;
  border-radius: 1px;
}
.header__burger span:last-child {
  margin-bottom: 0;
}
.header__logo {
  min-width: 178px;
  font-weight: 700;
  font-size: 8px;
  line-height: 8px;
  text-transform: uppercase;
}
.header__logo img {
  display: block;
  margin-bottom: 4px;
}
.header__logo br {
  display: none;
}
.header__info {
  display: flex;
  align-items: center;
}
.header__contacts {
  display: none;
}
.header__contacts-right {
  display: none;
}
.header__contacts-tel {
  margin-bottom: 4px;
  font-weight: 700;
  font-size: 14px;
  line-height: 100%;
  text-transform: uppercase;
  color: #000000;
}
.header__contacts-tel__icon {
  width: 36px;
  height: 36px;
  display: none;
  background: var(--bgColor);
  border-radius: 3px;
}
.header__contacts-time {
  font-weight: 500;
  font-size: 10px;
  line-height: 120%;
  color: #878787;
}
.header__user {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-left: auto;
}
.header__btn {
  margin-right: 8px;
}
.header__btn:last-child {
  margin-right: 0;
}
.header__btn--tel {
  background: transparent;
  border: 1px solid var(--textColor);
}
.header__btn-text {
  display: none;
}
.header__bottom {
  padding: 14px 30px 30px;
  height: calc(100vh - 56px);
  display: none;
  position: fixed;
  z-index: 10;
  top: 56px;
  left: 0;
  right: 0;
  background: #fff;
  overflow-y: auto;
}
.header__bottom-contacts--tel {
  background: transparent;
  border: 1px solid var(--textColor);
  color: var(--textColor);
}
.header__bottom-contacts li {
  margin-bottom: 14px;
}
.header__bottom-contacts li:last-child {
  margin-bottom: 0;
}
.header__bottom-contacts .link {
  width: 100%;
  padding: 17px 20px;
  font-size: 15px;
  font-weight: 600;
}
.header__menu {
  margin-bottom: 24px;
}
.header__menu-toggle {
  display: block;
  width: 100%;
  padding: 16px 0;
  font-weight: 500;
  font-size: 12px;
  line-height: 100%;
  text-transform: uppercase;
}
.header__menu-item {
  border-bottom: 1px solid var(--bgColor);
}
.header__menu-item:last-child {
  border-bottom: none;
}
.header__menu-submenu {
  padding-bottom: 16px;
  font-weight: 400;
  font-size: 13px;
  line-height: 200%;
  color: var(--textColor);
}
.header__inner {
  padding: 10px var(--padding);
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #fff;
  position: relative;
  z-index: 5;
}
.header__inner--active::after {
  content: "";
  height: 15px;
  position: absolute;
  z-index: 15;
  top: 41px;
  left: 0;
  right: 0;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.05);
}
.header__inner--active .header__logo {
  display: none;
}
.header__inner--active .header__contacts {
  display: block;
}
.header__inner--active .header__bottom {
  display: block;
}

@media screen and (min-width: 1024px) {
  .header__lead {
    display: block;
    background: var(--bgColor);
    font-weight: 300;
    font-size: 11px;
    line-height: 109%;
    color: var(--textColor);
  }
  .header__lead-text {
    padding: 10px var(--padding);
  }
  .header__inner {
    padding: 24px var(--padding);
  }
  .header__btn {
    padding: 12px 20px;
    margin-right: 12px;
    width: auto;
    text-align: center;
    font-weight: 600;
    font-size: 12px;
    line-height: 100%;
    color: #fff;
  }
  .header__btn--tel {
    color: var(--textColor);
  }
  .header__btn svg {
    display: none;
  }
  .header__btn-text {
    display: block;
  }
  .header__left {
    margin-right: 55px;
  }
  .header__burger {
    display: none;
  }
  .header__logo {
    min-width: 140px;
    font-size: 13px;
    line-height: 123%;
  }
  .header__logo img {
    width: 136px;
    margin-bottom: 12px;
  }
  .header__logo br {
    display: inline;
  }
  .header__info {
    margin-bottom: 28px;
  }
  .header__contacts {
    display: block;
  }
  .header__contacts-left {
    display: flex;
    align-items: center;
  }
  .header__contacts-tel {
    margin-bottom: 0;
    margin-right: 16px;
    display: flex;
    align-items: center;
    font-size: 16px;
  }
  .header__contacts-tel__icon {
    margin-right: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .header__contacts-time {
    font-weight: 500;
    font-size: 12px;
  }
  .header__contacts svg {
    fill: #878787;
    width: 26px;
    height: 26px;
  }
  .header__bottom {
    padding: 0;
    position: static;
    display: block;
    height: auto;
    overflow-y: visible;
  }
  .header__bottom-contacts {
    display: none;
  }
  .header__menu {
    margin-bottom: 0;
    display: flex;
  }
  .header__menu-submenu {
    display: none;
  }
  .header__menu-item {
    margin-right: 36px;
    border-bottom: none;
  }
  .header__menu-item:last-child {
    margin-right: 0;
    margin-left: auto;
  }
  .header__menu-toggle {
    padding: 0;
  }
}
@media screen and (min-width: 1366px) {
  .header__lead {
    font-size: 12px;
  }
  .header__left {
    margin-right: 76px;
  }
  .header__contacts {
    display: flex;
    align-items: center;
  }
  .header__contacts-left {
    margin-right: 20px;
  }
  .header__contacts-right {
    display: flex;
  }
  .header__contacts-location {
    display: flex;
    align-items: center;
    font-size: 12px;
  }
  .header__contacts-location__icon {
    width: 36px;
    min-width: 36px;
    height: 36px;
    margin-right: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--bgColor);
    border-radius: 3px;
  }
}
@media screen and (min-width: 1600px) {
  .header__inner {
    padding: 40px var(--padding);
  }
  .header__left {
    margin-right: 74px;
  }
  .header__contacts-left {
    margin-right: 40px;
  }
  .header__contacts-tel {
    margin-right: 24px;
  }
  .header__contacts-tel__icon {
    margin-right: 24px;
  }
}
.finance {
  margin-bottom: 14px;
}
.finance__info {
  padding-top: 13px;
  padding-bottom: 19px;
  background: var(--bgColor);
  border-radius: 0px 0px 50px 0px;
}
.finance__title {
  margin-bottom: 12px;
  font-size: 14px;
  line-height: 143%;
  font-weight: 300;
}
.finance__title br {
  display: none;
}
.finance__percent {
  margin-bottom: 12px;
  font-weight: 800;
  font-size: 50px;
  line-height: 100%;
  letter-spacing: -0.06em;
}
.finance__text {
  max-width: 400px;
  margin-bottom: 12px;
  font-size: 11px;
  line-height: 127%;
}
.finance__slider {
  position: relative;
}
.finance__slider-container {
  position: relative;
  overflow: hidden;
}
.finance__toggle {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: #fff;
  top: calc((100% - 216px)/2);
}
.finance__toggle-prev {
  left: 17px;
}
.finance__toggle-next {
  right: 15px;
}
.finance__toggle::after {
  content: "";
}
.finance__toggle svg {
  width: 16px;
  height: 16px;
}
.finance__img img {
  display: block;
  width: 100%;
}

@media screen and (min-width: 1024px) {
  .finance__inner {
    padding: 0 var(--padding);
  }
  .finance__slide {
    display: flex;
    flex-direction: row-reverse;
  }
  .finance__img {
    width: calc(100% - 392px);
    min-width: calc(100% - 392px);
    position: relative;
    overflow: hidden;
    border-bottom-right-radius: 50px;
  }
  .finance__img picture {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  .finance__img picture img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
  .finance__info {
    padding: 42px 10px 40px 50px;
    border-radius: 0;
  }
  .finance__title {
    margin-bottom: 8px;
    font-size: 18px;
    line-height: 111%;
  }
  .finance__title br {
    display: inline;
  }
  .finance__percent {
    margin-bottom: 14px;
    font-size: 60px;
    line-height: 120%;
  }
  .finance__text {
    margin-bottom: 30px;
    font-size: 12px;
    line-height: 133%;
  }
  .finance__toggle {
    width: 52px;
    height: 52px;
    top: 50%;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.15);
  }
